import React from "react";

import css from "./style.module.scss";

const createCells = (value: string, count: number) => {
  const multiplier = 0.2;
  let defaultValue = 1.8;
  let cells: string[] = [];

  if (+defaultValue < +value) {
    defaultValue = Math.ceil(+value);
  }
  // defaultValue += "x";
  for (let i = 0; i < count; i++) {
    cells.push(Number(defaultValue - multiplier * i).toFixed(1));
  }
  return cells;
};
export const YData = ({ value = "1.76" }: { value: string }) => {
  return (
    <div className={css.root}>
      {createCells(value, 4).map((cell) => {
        return (
          <div key={`y-${cell}`} className={css.cell}>
            {cell}
          </div>
        );
      })}
    </div>
  );
};
