import React, { FC } from "react";

import { ReactComponent as Oval } from "./oval.svg";
import { XData } from "./components/XData";
import { YData } from "./components/YData";
import { ICrashResult } from "../types";

import css from "./style.module.scss";

export interface ICrashComponentProps extends ICrashResult {}
export const CrashComponent: FC<ICrashComponentProps> = ({}) => {
  return (
    <div className={css.root}>
      <span className={css.prevValue}>
        <Oval />
        {prevValue}
      </span>
      <span className={css.result}>{value + "x"}</span>
      <XData></XData>
      <YData value={value}></YData>
    </div>
  );
};

const testData = {
  value: "1.98",
  prevValue: "2.52",
  win: true,
};
const value = "1.98";
const prevValue = "2.52";
