import React from "react";

import css from "./style.module.scss";
const numbers = [0, 2, 4, 6, 7, 10];
export const XData = () => {
  return (
    <div className={css.root}>
      {numbers.map((num) => {
        return (
          <div key={`x-${num}`} className={css.cell}>
            {num}
          </div>
        );
      })}
    </div>
  );
};
